
import BScroll from 'better-scroll'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

export default {
name: 'Mine',
components: {
CheckIcon
},
data () {
return {
pulldownMsg: '下拉刷新',
pullupMsg: '加载更多',
alertHook: 'none',
isMoreData: true,
moneyInfo: {
balance: '', //账户余额
withDrawCash: '', //提现中金额
items: []
},
params: {
merchantId: '',
startDate: '',
endDate: '',
status: 1,
page: 1,
pageSize: 10
}
}
},
created () {
this.$vux.loading.show({
text: '加载中...'
})
this.params.merchantId = this.$store.state.merchantId
this.getData()
},
methods: {
getMoney(){
this.$router.push({
name: 'getMoney'
})
},
test(status){
this.params.status = status
this.refreshData()
},
getBack () {
this.$router.go(-1)
},
getData () {

this.$http.fetchGet('/merchant/center/get/balance/detail', this.params).then((res) => {
this.moneyInfo = res.data.data
if (this.moneyInfo.items.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
this.$nextTick(() => {
if (this.$refs.list_con.offsetHeight > this.$refs.content.offsetHeight) {
this.$refs.content.style.height = this.$refs.list_con.offsetHeight + 2 + 'px'
}
})
}
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
this.initScroll()
})
},
myMoney(){
this.$router.push({
name: 'myMoney'
})
},
certification () {
this.$router.push({
name: 'certification'
})
},
goSetting () {
this.$router.push({
name: 'Setting'
})
},
initScroll () {
this.$nextTick(() => {
this.scroll = new BScroll(this.$refs.wrapper, { // 初始化better-scroll
probeType: 1, // 1 滚动的时候会派发scroll事件，会截流。2滚动的时候实时派发scroll事件，不会截流。 3除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
click: true 
})

this.scroll.on('scroll', (pos) => {
if (pos.y > 30) {
this.pulldownMsg = '释放立即刷新'
}
})

this.scroll.on('touchEnd', (pos) => { // 上拉刷新
if (pos.y > 30) {
this.refreshData()
} else if (pos.y < (this.scroll.maxScrollY - 30)) { // 下拉加载
this.loadMoreData()
}
})
})
},
refreshData () {
this.$refs.content.style.height = 'auto'
this.pullupMsg = '加载中。。。'
this.params.page = 1
this.$http.fetchGet('/merchant/center/get/balance/detail', this.params).then((res) => {
this.moneyInfo = res.data.data
if (this.moneyInfo.items.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
this.$nextTick(() => {
if (this.$refs.list_con.offsetHeight > this.$refs.content.offsetHeight) {
this.$refs.content.style.height = this.$refs.list_con.offsetHeight + 2 + 'px'
}
})
}

this.pullupMsg = '加载更多'

this.scroll.refresh()
})
},
loadMoreData () {
if (this.moneyInfo.items.length < this.params.pageSize || this.moneyInfo.items.length < this.params.pageSize * this.params.page) {
this.isMoreData = false
return
}
this.params.page++
this.$http.fetchGet('/merchant/center/get/balance/detail', this.params).then((res) => {
if (this.moneyInfo.items.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
}
if (res.data.data.items.length > 0) {
res.data.data.goodsList.map((item) => {
this.moneyInfo.items.push(item)
})
} else {
this.isMoreData = false
}

this.pulldownMsg = '下拉刷新'

this.scroll.refresh()
})
}
}
}



